<template>
    <div class="container">
        <div class="main-expenses">
            <div class="top box">
                <h1 class="main-title">Expense Tracker</h1>
                <h3>Your balance</h3>
                <p>$ {{ getBalance | toDecimal(2) }}</p>
            </div>
            <div class="box-in-out box">
                <div id="income">
                    <h4>Income</h4>
                    <p>$ {{ getIncomes | toDecimal(2) }}</p>
                </div>
                <div id="expense">
                    <h4>Expense</h4>
                    <p>$ {{ getExpenses | toDecimal(2) }}</p>
                </div>
            </div>
            <div class="history box">
                <h2 class="secondary-title">History</h2>
                <div class="history-items">
                    <div class="item"
                         :class="{ 'negative' : item.type === 'expenses', 'positive' : item.type === 'income'   }"
                         v-for="(item, idx) in orderTransactions" :key="idx">
                        <h4 class="center-vertical">{{ item.desc }}</h4>
                        <p class="center-vertical">{{ item.amount }}</p>
                    </div>
                </div>
            </div>
            <form @submit.prevent="sendTransaction" id="add-transaction" class="box">
                <h2 class="secondary-title">Add new Transaction</h2>
                <label for="text">Text</label>
                <input class="form-control" type="text" id="text" v-model="transaction.desc">
                <label for="amount">Amount</label>
                <p>(negative - expense, positive - income)</p>
                <input  class="form-control" type="number" id="amount" v-model.number="transaction.amount">
                <input :disabled="transaction.desc.length === 0 && transaction.amount !== 0" class="btn-primary" type="submit" value="Add Transaction">
            </form>
        </div>
    </div>
</template>

<script>
    const insertItem = (arr, item) => {
        arr.push(item)
        return arr;
    }

    const isEmpty = text => text.length === 0;

    const equalZero = cant => cant === 0;

    const isExpense = amount => amount < 0;

    const sumByType = (arr, type) => {
        let sum = 0;
        for (let i = 0; i < arr.length; i++) {
            if (arr[i].type === type) sum += arr[i].amount;
        }
    }

    const totalExpenses = (arr) => {
        let sum = 0;
        for (let i = 0; i < arr.length; i++) {
            if (arr[i].type === 'expenses') sum += arr[i].amount;
        }
        return sum;
    }

    const totalIncomes = arr => {
        let sum = 0;
        for (let i = 0; i < arr.length; i++) {
            if (arr[i].type === 'income') sum += arr[i].amount;
        }
        return sum
    }


    export default {
        name: "Expense",
        data() {
            return {
                balance: 0,
                overall: {
                    income: 0,
                    expenses: 0
                },
                transaction: {
                    desc: '',
                    amount: null
                },
                transactions: []
            }
        },
        computed: {
            orderTransactions() {
                if (!isEmpty(this.transactions))
                    return this.transactions.reverse();
                return this.transactions;
            },
            getExpenses() {
                if (!isEmpty(this.transactions))
                    return totalExpenses(this.transactions)
                return 0;
            },

            getIncomes() {
                if (!isEmpty(this.transactions))
                    return totalIncomes(this.transactions);
                return 0;
            },

            getBalance() {
                if (!isEmpty(this.transactions))
                    return this.getIncomes - this.getExpenses;
                return 0;
            }
        },
        filters: {
            toDecimal(value, n) {
                return value.toFixed(n);
            }
        },
        methods: {
            sendTransaction() {
                try {
                    this.handleTransactionOperation()
                } catch (e) {
                    alert(e);
                    console.log('Error:' + e)
                }
            },

            handleTransactionOperation() {
                const newTransaction = this.getFieldsValues();
                this.transactions = insertItem(this.transactions, newTransaction);
                this.clearFields();
            },

            getFieldsValues() {
                if (this.isValidFields()) {
                    if (isExpense(this.transaction.amount)) {
                        return {
                            desc: this.transaction.desc,
                            amount: Math.abs(this.transaction.amount),
                            type: 'expenses'
                        }
                    } else {
                        return {
                            desc: this.transaction.desc,
                            amount: this.transaction.amount,
                            type: 'income'
                        }
                    }
                } else throw 'Incorrect values in transaction'
            },

            isValidFields(){
                return !isEmpty(this.transaction.desc) && !equalZero(this.transaction.amount) && this.transaction.amount !== null
            },

            clearFields() {
                this.transaction.desc = '';
                this.transaction.amount = null;
            }


        }
    }


</script>

<style scoped>
    .container {
        max-width: 1200px;
        margin: 0 auto;
    }

    .box {
        margin-bottom: 30px;
    }

    .main-expenses {
        max-width: 350px;
        margin: 0 auto;
    }

    .main-title {
        font-size: 22px;
        color: #2c3e50;
    }

    .secondary-title {
        font-size: 16px;
        color: #555;
        padding-bottom: 5px;
        margin-bottom: 5px;
        border-bottom: 1px solid rgba(204, 204, 204, 0.95);
    }

    .form-control {
        width: calc(100% - 15px);
        border: 1px solid #ccc;
        border-radius: 5px;
        height: 30px;
        padding-left: 15px;
        margin: 10px 0;
    }

    .btn-primary {
        width: 100%;
        border: none;
        border-radius: 5px;
        margin-top: 10px;
        height: 35px;
        color: #fff;
        background: #265ae0;
        cursor: pointer;
    }

    .btn-primary:hover {
        background: #4576f3;

    }

    .top {
        color: #555;
    }

    .top h3 {
        font-size: 16px;
        margin: 0;
    }

    .top p {
        font-size: 25px;
        margin-top: 5px;
        color: #2c3e50;
        font-weight: bold;

    }

    .box-in-out {
        box-shadow: 1px 1px 4px #d4d3d3;
        padding: 15px;
        border-radius: 5px;
        /*  flex */
        display: flex;
        flex-wrap: nowrap;
        justify-content: center;
        text-transform: uppercase;
    }


    #income {
        text-align: center;
        padding-right: 40px;
        border-right: 1px solid #ccc;
    }

    #income h4 {
        color: #2c3e50;
        font-weight: bold;
        margin: 0;
    }

    #income p {
        color: #42b983;
        margin: 0;
    }

    #expense {
        text-align: center;
        padding-left: 40px;
    }

    #expense h4 {
        color: #2c3e50;
        font-weight: bold;
        margin: 0;
    }

    #expense p {
        color: crimson;
        margin: 0
    }

    .history-items {
        /*height: 200px;
        overflow-y: scroll;*/

    }


    .history .item {
        box-shadow: 1px 1px 4px #ccc;
        padding: 0 15px;
        margin-top: 10px;
        border-radius: 5px;

        display: flex;
        justify-content: space-between;
    }

    .center-vertical {
        padding: 13px;
        margin: 0;
    }

    .negative {
        border-right: 5px solid #f83232;
    }

    .positive {
        border-right: 5px solid #64ce4a;
    }

    #add-transaction p {
        margin: 0;
    }


</style>